.delete-btn {
  float: right;
  color: #e74944;
  cursor: pointer;
}

.jumbotron {
  background: transparent;
  background-size: cover;
  height: 300;
  clear: both;
  padding-top: 65vh;
  text-align: center;
}
p {
  text-align: center;
  font-size: 3em;
  margin: 20px 0 20px 0;
}

a {
  text-decoration: none;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}

a:hover {
  color: #ff1177;
  font-family: Monoton;
  -webkit-animation: neon1 1.5s ease-in-out infinite alternate;
  animation: neon1 1.5s ease-in-out infinite alternate;
}

a {
  color: #fff;
  font-family: "Monoton", cursive;
  -webkit-animation: none;
  animation: none;
}

.non-link {
  color: #fff;
  font-family: "Monoton", cursive;
  float: center;
}

.notes {
  font-family: "Monoton", cursive;
  font-size: 2rem;
  color: red;
  float: right;
}

.back-link {
  color: red;
  font-size: 1.5rem;
  margin-bottom: 10vh;
  float: right;
}

.description {
  color: white;
  font-size: 1.5rem;
  margin: 5;
  float: right;
}

.judged {
  color: red;
  font-family: "Monoton", cursive;
  float: center;
}
.brewed {
  color: red;
  font-family: "Monoton", cursive;
  float: center;
  font-size: 6rem;
}


html,
body {
  height: 100%;
}
body {
  background-image: url(https://images.unsplash.com/photo-1554127959-b04104f23bab?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1001&q=80);
  background-position: center center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
}

div,
body {
  margin: 0;
  padding: 0;
  font-family: exo, sans-serif;
}
.wrapper {
  height: 100%;
  width: 100%;
}


.list-overflow-container {
  max-height: 545px;
  border-radius: 4px;
  overflow-x: hidden;
  opacity: .75;
}

.list-overflow-container li * :hover {
  cursor: pointer;
  color: red;
}


.list-overflow-container li a {
  color: #2A878C;
  font-size: 1.5rem;
}

.list-overflow-container li:last-child {
  margin-bottom: 10vh;;
} 

.form-control {
  opacity: 0.75;
  background-color: white;
}

.btn {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 40%;
  background: greenyellow;
  margin-bottom: 5vh;
}


.question {
    color: red;
    font-family: 'Monoton', cursive;
    float: left;
    font-size: 2.1rem;
}

.values {
    color: red;
    float: center;
    font-size: 1rem;
    float: left;
    margin: 10px;
    opacity: .75;
}

.navbar {
    background-color: white;
    opacity: .9;
    height: 15vh;
}

.navbar-ref {
    color: red;
    font-size: 2.5rem;
    padding-right: 30px;
}

.login {
    color: red;
    font-size: 1.5rem;
    float: right;
}

